(() => {
    const refs = {
      openModalBtn: document.querySelector("[data-modal-open='888']"),
      closeModalBtn: document.querySelector("[data-modal-close='888']"),
      modal: document.querySelector("[data-modal='888']"),
    };
  
    refs.openModalBtn.addEventListener("click", toggleModal);
    refs.closeModalBtn.addEventListener("click", toggleModal);
  
  function toggleModal() {
      refs.modal.classList.toggle("is-hidden");
    }
  })();